/*
*   Name: Bizagi Grid for Smartphone
*   Author: Bizagi UI Forms
*   Comments:
*   -   This widget will extend the bizagi grid widget to specialize into a smartPhone view
*
*   
*
*   Styles:
*   - css/bizagi/smarpthone/controls/bizagi.grid.css
*/

(function ($) {
    $.widget('ui.bizagi_grid_smartphone', $.ui.bizagi_grid, {

        /*
        *   Method to attach any handler to the grid
        */
        attachHandlers: function () {
            var self = this;

            // Add paging events
            var pager = self._getComponent("pager");
            pager.on("click", "div.bz-btn-icn", function () {
                var pageNumber = $(this).attr("data-page-number");
                if (pageNumber && self.isValid()) {
                    self.changePage(pageNumber);
                }
            });

            pager.on("change", "input", function () {
                var page = this.value;
                if (page && self.isValid()) {
                    var _input = this;
                    self.changePage(page, /*success*/function () { }, /*fail*/function () {
                        _input.value = self.page;
                    });
                }
            });

            if (self._getComponent("buttons") != undefined) {
                var items = [];
                if (self.options.actions.add) {
                    items.push({
                        name: bizagi.localization.getResource("render-grid-add-label"),
                        fn: function () {
                            self.processAction("add");
                            self.unselectRow();
                        }
                    });
                }

                if (self.options.actions.hasTotalizer) {
                    items.push({
                        name: "Totalizer", // TODO: Add resource
                        fn: function () {
                            self.totalizer();
                        }
                    });
                }

                self._getComponent("buttons").find(".bz-btn-icn").overflowMenu({ items: items });
            }

            // Bind special cell button action
            var _row_buttons = self._getComponent("rowButtons");
            if (_row_buttons){
                _row_buttons.on("click", "[data-action]", function () {
                    self.processAction($(this).attr("data-action"), {});
                });
            } 
        },
        /* popup Total
         *
         */
        totalizer: function () {
            var self = this;
            self.options.totalData();
        },

        /*totalData
         *   prepare totalizer
        */
        draw: function () {
            var self = this;
            self._super();
            var verifyTotalizer = self.options.actions.totalizer;
            if (self.data.rows.length == 0) {
                var rowButtons =  self._getComponent("rowButtons");
                if (rowButtons){
                    rowButtons.hide();
                }
                self.element.addClass("bz-mobile-grid--empty");
            }
            //Verify totalizer to activate action sheet
            if (verifyTotalizer) {
                if (typeof self.actualActionSheet != "undefined" && !bizagi.util.isObjectEmpty(self.actualActionSheet)) {
                    self.actualActionSheet.data("kendoMobileActionSheet").destroy();
                }

                self.actualActionSheet = self.element.find("#ui-bizagi-menu-totalizer").kendoMobileActionSheet();
            }

            //hide loader
            bizagi.util.smartphone.stopLoading();
        },

        drawRow: function (rowData, isNewRow) {
            this.selectedBusinessKey = rowData[this.columnKeyIndex];
            return this._super(rowData, isNewRow);
        },

        changePage: function (page, success_callback, fail_callback) {
            var self = this;
            var currentPage = Number(self.data.page);
            //in the smartphone the original  self.data.total is always 1  this change to self.data.records refers in this case a total pages
            var total = self.data.records;
            var newPage = page;

            // Process previous page
            if (page == "previous") {
                if (currentPage > 1) newPage = currentPage - 1;
                else {
                    if (fail_callback) fail_callback();
                    return;
                }
            }
            // Process next page
            if (page == "next") {
                if (currentPage < total) newPage = currentPage + 1;
                else {
                    if (fail_callback) fail_callback();
                    return;
                }
            }

            if (page !== "next" && page !== "previous" && typeof page === "string") {
                try {
                    newPage = Number(page);
                }
                catch (e) {
                    if (fail_callback) fail_callback();
                    return;
                }
                if (newPage == NaN || newPage > total || newPage < 0) {
                    if (fail_callback) fail_callback();
                    return;
                };
            }

            //show loader
            bizagi.util.smartphone.startLoading();
            setTimeout(function () {
                bizagi.util.smartphone.stopLoading();
            }, 1000);

            // Update internal options
            self.page = Number(newPage);

            // Execute handler
            self.onPageRequested();
            if (success_callback) {
                success_callback();
            }
        },

        /*
        *   Executes the row selected handler
        */
        onRowSelected: function () {
            var self = this;
            // Call base
            $.ui.bizagi_grid.prototype.onRowSelected.apply(self, arguments);
        },

        /*
        *   Executes the row un-selected handler
        */
        onRowUnselected: function () {
            var self = this;
            // Call base
            $.ui.bizagi_grid.prototype.onRowUnselected.apply(self, arguments);
        },

        /*
        *  Template method to check if buttons component must be rendered
        */
        _canShowButtons: function () {
            var self = this;
            var actions = self.options.actions;
            return actions["add"] || actions["hasTotalizer"];
        },

        /*
        *  Template method to check if row buttons must be displayed when the row is selected
        */
        _canShowRowButtons: function () {
            var self = this;
            var actions = self.options.actions;
            return actions["edit"] || actions["remove"] || actions["details"];
        },

        /*
        *   Return the new created ids
        */
        getNewRowKeys: function () {
            var self = this;
            var rows = self._getComponent("rows").find("[data-new-row]");
            return $.map(rows, function (row, i) {
                return $(row).data("business-key");
            });
        },


        /*
        *   Show the row buttons
        */
        _showRowButtons: function () {
        },

        /*
        *   Hide the row buttons
        */
        _hideRowButtons: function () {
        },

        /**
         * Replaces a matched element in the container for the specified "replace" element
         * @param html
         * @param element
         * @param replace
         * @returns {*}
         * @private
         */
        _replaceElement: function (html, element, replace) {
            return html.replace(new RegExp("{{element " + element + "}}", 'g'), replace);
        },
        
        /**
         * Check if row its new
         * @param {} businessKey
         * @returns {}
         */
        isNewRecord: function (businessKey) {
            var self = this;
            var row = self._getComponent("rows");

            if (row) {
                businessKey = row.find("[data-business-key='" + businessKey + "']");
                if ($(businessKey).data("new-row")) {
                    return true;
                }
            }
            return false;
        }
    });

})(jQuery);
